/*>>>>>>>> Reset <<<<<<<<<*/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
    line-height: 28px;
}

p{
    margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    line-height: 1;
}

a,
span {
    display: inline-block;
}

a {
    transition: 0.4s;
    color: inherit;
    &:focus {
        outline: none !important;
    }
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
     margin: 0;
}

// a:link {
//   color: $primary-color;
// }
a:hover {
    text-decoration: none;
}

a:visited {
    // color: $primary-color;
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
}

img {
    max-width: 100%;
}

p {
    // margin: 15px 0;
    line-height: 1.75;
}

.btn:focus,
.btn:active {
    box-shadow: none;
    // border-color: transparent;
}

button {
    cursor: pointer;
    background: transparent;
    border: none;
    &:focus {
        outline: none;
    }
}



