input[type="number"] {
    -moz-appearance: textfield;
}

/* No gutters */

.no-gutters-lg {
    @include desktops {
        >.col,
        >[class*="col-"] {
            padding-right: 0;
            padding-left: 0;
        }
    }
    @include large-desktops {
        >.col,
        >[class*="col-"] {
            padding-right: 0;
            padding-left: 0;
        }
    }
    @include extra-large-desktops {
        >.col,
        >[class*="col-"] {
            padding-right: 0;
            padding-left: 0;
        }
    }
}

.nice-select {
    display: flex;
    align-items: center;
    float: none;
}

#scrollUp {
    bottom: 80px;
    right: 10px;
    padding: 10px 15px;
    text-align: center;
    cursor: pointer;
    border: 2px solid #ebebeb;
    color: #292929;
    transform: rotate(-90deg);
    font-weight: 600;
    text-transform: uppercase;
    background: rgba(255, 255, 255, .9);
    z-index: 9;
    font-size: 12px;
    transition: .4s;

    &:hover {
        color: #fff;
        background: $primary-color;
        border-color: $primary-color;
    }
}

.form-control {
    display: inline-block;
    padding: 10px 15px;
    line-height: 30px;
    height: 52px;
    color: #7b7b7b;
    font-size: 15px;
    width: 100%;
    border: 2px solid #eeeeee;
    background: #fff;
    border-radius: 3px;
    box-shadow: none;
    &:focus {
        box-shadow: none;
        outline: none;
        border: 2px solid #eeeeee;
    }
}

@mixin placeholder {
    ::-webkit-input-placeholder {@content};
    :-moz-placeholder           {@content};
    ::-moz-placeholder          {@content};
    :-ms-input-placeholder      {@content} ; 
  }