.grid-margin {
    margin-bottom: 1.875rem;
}
.stretch-card {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: stretch;
    align-items: stretch;
    -webkit-justify-content: stretch;
    justify-content: stretch;
}
.stretch-card > .card {
    width: 100%;
    min-width: 100%;
}
.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid #e7eaed;
    border-radius: 0;
    .card-body {
        padding: 24px 30px;
    }
    .card-title {
        color: #4a4a4a;
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 24px;
        font-size: 14px;
    }
    h1 {
        font-size: 40px;
        color: #000000;
        margin-bottom: 10px;
    }
    h4 {
        font-size: 20px;
        color: #000000;
        margin-bottom: 7px;
    }
}

//Table css
.table {
    margin-bottom: 0;
    width: 100%;
    color: #212529;
    td {
        font-size: 14px;
    }
}
.table thead th {
    border: 1px solid rgba(151, 151, 151, 0.18);
    padding: 1.7rem 0.9375rem;
    white-space: nowrap;
    position: relative;
    cursor: pointer;
    font-weight: 500;
    font-size: 16px;
}
.table tbody tr:nth-child(odd) {
    background: rgba(238, 238, 238, 0.57);
}
.table tbody tr td {
    border: 1px solid rgba(151, 151, 151, 0.18);
    color: #08113b;
    white-space: nowrap;
    font-weight: 300;
    padding: 1.1rem 0.9375rem;
}
.table thead th:after, .table thead th:before {
    content: "\F05D";
    right: 25px;
    bottom: 32px;
    position: absolute;
    line-height: 2.5;
    font-family: Material Design Icons;
    font-size: 10px;
}
.table thead th:before {
    content: "\F045";
    right: 21px;
    bottom: 26px;
}


/*=========.breadcrumb-css=============*/ 
.breadcrumb-row {
    margin-bottom: 10px;
    .breadcrumb-btn{
        @include mx-mobile-xs{
            justify-content: start !important;
        }
        @include mx-mobile{
            justify-content: start !important;
        }
    }
}
.breadcrumb-row h3 {
    font-size: 21px;
    line-height: 30px;
    font-weight: 400;
}
.breadcrumb-row .breadcrumb {
    background: transparent;
    padding: 0;
    margin-bottom: 0;
}
.breadcrumb-row .breadcrumb li {
    margin: 5px 0;
    font-size: 16px;
}
.breadcrumb-row .breadcrumb li i {
    font-size: 20px;
}
.breadcrumb-item.active {
    color: #6c757d;
}
.breadcrumb-row .breadcrumb li a {
    color: $primary-color;
}



/*============== .nav-tabs  ================*/
.nav-tabs {
    border: 1px solid #f3f3f3;
    .nav-item {
        margin-bottom: -1px;
        .nav-link {
            color: #000000;
            border-radius: 0;
            padding: 1rem 1.5rem .2rem 1.5rem;
            border: 0;
            background: transparent;
            border-bottom: 3px solid transparent;
            &.active {
                border: 0;
                border-bottom: 3px solid $primary-color;
                color: $primary-color;
            }
        }
    }
}

.dashboard-tabs {
    small {
        line-height: 20px;
        margin: -3px 0 1px 0 !important;
    }
    .datedropdown {
        small {
            line-height: 14px;
            margin: 6px 0 0 0 !important;
        }
    }
    h5 {
        font-size: 16px;
    }
    i{
        font-size: 40px;
        margin-right: 20px;
    }
    .btn {
        height: 18px;
    }
    .dropdown-menu.show {
        top: 10px !important;
    }
}


// footer
.footer {
    background: #f3f3f3;
    padding: 30px 1rem;
    transition: all 0.25s ease;
    -moz-transition: all 0.25s ease;
    -webkit-transition: all 0.25s ease;
    -ms-transition: all 0.25s ease;
    font-size: calc(0.875rem - 0.05rem);
    font-weight: 400;
    border-top: 1px solid rgba(0, 0, 0, 0.06);
    .footerlogo {
        img {
            max-width: 100px;
        }
    }
}
