
// off-canvas-wrapper css
.off-canvas-wrapper{
    height: calc(100vh - 50px);
    background: #ffffff;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    padding: 0;
    width: 257px;
    z-index: 11;
    transition: width 0.25s ease, background 0.25s ease;
    -webkit-transition: width 0.25s ease, background 0.25s ease;
    -moz-transition: width 0.25s ease, background 0.25s ease;
    -ms-transition: width 0.25s ease, background 0.25s ease;
    box-shadow: none;
    border-right: 1px solid #e3e3e3;
    &.active {
        right: 0;
    }
    @include all-below-med{
        position: fixed;
        height: calc(100vh - 50px);
        top: 60px;
        bottom: 0;
        overflow: auto;
        right: -257px;
        -webkit-transition: all 0.25s ease-out;
        -o-transition: all 0.25s ease-out;
        transition: all 0.25s ease-out;
        
    }
}

.user-details-block{
    padding: 25px 12px 12px 12px;
    border-bottom: 1px solid #f3f3f3;
    .image{
        width: 50px;
        border-radius:100%; 
        overflow: hidden;
        margin: 0 auto;
    }
    .user-title{
        font-size: 17px;
        font-weight: 500;
        margin-top: 5px;
    }
}

.sidemenu{
    > li{
        padding: 12px 40px 12px 20px;
        border-bottom: 1px solid#f3f3f3;
        position: relative;
        i{
            font-size: 18px;
            margin-right: 12px;;
        }
        >a{
            display: flex;
            align-content: center;
        }
    }
    a{
        &:hover{
            color: $primary-color;
        }
    }
    ul.sub-menu {
        padding-left: 25px;
        list-style: disc;
        // color:#9b9b9b;
        display: none;
    }
    span.menu-expand {
        position: absolute;
        right: 20px;
        top: 12px;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        i {
            margin: 0;
            // color: #9b9b9b;
        }
    }
    
}


.icon-nav{
    .sidemenu > li {
        padding:10px 20px;
        border-bottom: 1px solid #f3f3f3;
        position: relative;
        display: flex;
        justify-content: center;
        &:hover{
            .menu-title{
                display: flex;
            }
        }
        a{
            i{
                margin: 0;
            }
        }
        >.sub-menu{
            margin-top: 37px;
        }
    }
    .menu-title {
        position: absolute;
        background: #ccc;
        z-index: 1000;
        top: 0;
        height: 100%;
        display: flex;
        align-items: center;
        padding-left: 15px;
        display: none;
        box-shadow:0px 3px 21px 0px rgba(0, 0, 0, 0.2);
    }
    .sub-menu{
        padding-left: 25px;
        list-style: disc;
        display: none;
        position: absolute;
        left: 100%;
        z-index: 999;
        padding: 15px 20px;
        box-shadow:0px 3px 21px 0px rgba(0, 0, 0, 0.2);
    }
    .sub-menu,.menu-title{
        display: none;
        background: #fff;
        left: calc(100% + 1px);
        width: 200px;
    }
    .menu-item-has-children{
       &:hover{
        > .sub-menu{
            display: block;
        }
       }
       a{
           .menu-title{
            box-shadow:3px -12px 13px -11px rgba(0, 0, 0, 0.2);
           }
       }
    }
    .menu-expand{
        visibility: hidden;
        z-index: -999;
    }
}