$alert-update: #a3dce6;
$alert-danger: rgba(228, 81, 95, 0.38823529411764707);
.site-alert{
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%; 
    z-index: 999;
    &.alert-update{
        background: $alert-update;
    }
    &.alert-danger{
        background: $alert-danger;
    }
    .alert-link{
        color: red;
    }
}