@media (max-width: 991px) {
  .main-panel {
    width: calc(100% - 0px);
  }
}

.main-panel .content-wrapper {
  background: #f3f3f3;
  padding: 2.1rem 1.04rem;
  width: 100%;
  -webkit-flex-grow: 1;
  flex-grow: 1;
}

.alert-active {
  padding-top: 40px;
}

.alert-active .dash-header {
  margin-top: 40px;
}

.grid-margin {
  margin-bottom: 1.875rem;
}

.stretch-card {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: stretch;
  justify-content: stretch;
}

.stretch-card > .card {
  width: 100%;
  min-width: 100%;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid #e7eaed;
  border-radius: 0;
}

.card .card-body {
  padding: 24px 30px;
}

.card .card-title {
  color: #4a4a4a;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 24px;
  font-size: 14px;
}

.card h1 {
  font-size: 40px;
  color: #000000;
  margin-bottom: 10px;
}

.card h4 {
  font-size: 20px;
  color: #000000;
  margin-bottom: 7px;
}

.table {
  margin-bottom: 0;
  width: 100%;
  color: #212529;
}

.table td {
  font-size: 14px;
}

.table thead th {
  border: 1px solid rgba(151, 151, 151, 0.18);
  padding: 1.7rem 0.9375rem;
  white-space: nowrap;
  position: relative;
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
}

.table tbody tr:nth-child(odd) {
  background: rgba(238, 238, 238, 0.57);
}

.table tbody tr td {
  border: 1px solid rgba(151, 151, 151, 0.18);
  color: #08113b;
  white-space: nowrap;
  font-weight: 300;
  padding: 1.1rem 0.9375rem;
}

.table thead th:after, .table thead th:before {
  content: "\F05D";
  right: 25px;
  bottom: 32px;
  position: absolute;
  line-height: 2.5;
  font-family: Material Design Icons;
  font-size: 10px;
}

.table thead th:before {
  content: "\F045";
  right: 21px;
  bottom: 26px;
}

