.page-body-wrapper{
    display: flex;
    flex-direction: row;
    padding-left: 0;
    padding-right: 0;
    padding-top: 50px;
}
.site-wrapper.sidebar-full{
    .brand-wrapper{
        width: 70px;
        .brand-inner{
            margin-right: 0;
            margin-left: 0;
            justify-content: center;
        }
    }
    .navbar-logo{
        display: none;
    }
    .header-content{
        width: calc(100% - 70px);
    }
    .off-canvas-wrapper{
        width: 70px;
    }

    .main-panel {
        width: calc(100% - 70px);
    }
}

.main-panel {
    transition: width 0.25s ease, margin 0.25s ease;
    width: calc(100% - 257px);
    min-height: calc(100vh - 60px);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    @include all-below-med{
        width: calc(100% - 0px);
    }
    .content-wrapper{
        background: #f3f3f3;
        padding: 2.1rem 1.04rem;
        width: 100%;
        -webkit-flex-grow: 1;
        flex-grow: 1;
    }
}

.alert-active{
    padding-top:40px; 
    .dash-header{
        margin-top: 40px;
    }
}