

.dash-header{
    display: flex;
    .brand-wrapper{
        background: #ffffff;
        border-bottom: 1px solid #e3e3e3;
        transition: width 0.25s ease, background 0.25s ease;
        width: 257px;
        height: 60px;
        border-right: 1px solid #e3e3e3;
        display: flex;
        justify-content: center;
        @include all-below-med{
            width: 55px;
        }
    }
    .navbar-logo{
        &-mini{
            display: none;
            @include all-below-med{
                display: block !important;
            }
        }
        img{
            // width: calc(257px - 150px);
            max-width: 150px;
            margin: auto;
            vertical-align: middle;
            height: 34px;
            
        }
    }
    .brand-inner{
        margin-left: 20px;
        margin-right: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        @include all-below-med{
            margin-left: 10px;
            margin-right: 10px;
        }
        .navbar-brand{
            color: #27367f;
            font-size: 1.5rem;
            margin-right: 0;
            padding: .25rem 0;
            &.navbar-logo-mini{
                @include all-below-med{
                    width: 35px;
                }
            }
            @include all-below-med{
                display: none;
            }
        }
    }
    .header-content{
        background: #ffffff;
        transition: width 0.25s ease;
        -webkit-transition: width 0.25s ease;
        -moz-transition: width 0.25s ease;
        -ms-transition: width 0.25s ease;
        color: #9b9b9b;
        padding-left: 1.062rem;
        padding-right: 1.062rem;
        width: calc(100% - 257px);
        height: 60px;
        border-bottom: 1px solid #e3e3e3;
        display: flex;
        @include all-below-med{
            width: calc(100% - 0px);
            padding-right: 70px;
        }
        .close-btn{
            color: inherit;
            font-size: 24px;
            position: absolute;
            right: 15px;
            top: 50%;
            margin-top: -17px;
        }
        .search-bar{
            display: flex;
            align-items: center;
            height: 100%;
            label{
                color: #9b9b9b;
                border: none;
                margin: 0;
                position: absolute;
                left: 0;
                height: 100%;
                width: 30px;
                text-align: center;
                line-height: 40px;
                cursor: pointer;
            }
            .input-group{
                background: #ececec;
                border-radius: 4px;
                padding: 0 .75rem;
                @include mx-mobile-xs{
                    display: none;
                }
                @include mx-mobile{
                    display: none;
                }
            }
            input{
                margin-left: 20px;
                height: 40px;
                width: 100%;
                flex: 1 1 auto;
                background: transparent;
                border: 0;
                &:focus{
                    outline: none;
                }
            }
            
        }
    }
}

.navbar-options{
    display: flex;
    flex-direction: row;
    align-items: stretch;
    .nav-item{
        display: flex;
        align-items: center;
        margin-right:16px;
        margin-left:16px;
        &.dropdown{
            .nav-link{
                display: flex;
                align-items: center;
                img{
                    max-width: 32px;
                    max-height: 32px;
                    border-radius: 50%;
                }
                &.count-indicator{
                    position: relative;
                    padding: 0;
                    text-align: center;
                    color: #9b9b9b;
                    i{
                        font-size: 24px;
                        margin-right: 0;
                        vertical-align: middle;
                        color: inherit;
                    }
                    &:hover{
                        color: #9b9b9b
                    }
                    .count{
                        position: absolute;
                        left: 59%;
                        width: 8px;
                        height: 8px;
                        border-radius: 100%;
                        background: $primary-color;
                        top: 0px;
                    }
                }
            }
        }
    }
    .navbar-dropdown{
        position: absolute;
        font-size: 0.9rem;
        margin-top: 0;
        right: 0;
        left: auto!important;
        top: 60px!important;
        .dropdown-item {
            margin-bottom: 0;
            padding: .65rem 1.5rem;
            cursor: pointer;
            display: flex;
            a{
                color: #212529;
            }
            .ellipsis{
                max-width: 200px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .item-thumbnail{
                img{
                    width: 36px;
                    height: 36px;
                    border-radius: 50%;
                    vertical-align: middle;
                } 
                .item-icon{
                    width: 36px;
                    height: 36px;
                    border-radius: 50%;
                    vertical-align: middle;
                    display: flex;
                    justify-content:center;
                    align-items: center;
                    i{
                        font-size: 17px;
                        color: #fff;
                    }
                }
            }
            &:hover{
                background: #eaeaf1;
            }
        }
    }
    .dropdown-menu{
        border: none;
        box-shadow: 0px 3px 21px 0px rgba(0, 0, 0, 0.2);
        min-width: 255px;
        animation-name: dropdownAnimation;
        animation-duration: 0.25s;
        animation-fill-mode: both;
        border-radius: 0;
        @include all-below-med{
            position: fixed !important;
            left: 0 !important;
            right: 0;
            width: calc(100% - 35px);
            margin: 0 auto;
        }
        .dropdown-header{
            color: #000;
        }
        .item-content{
            padding-left: 15px;
            width: calc(100% - 36px);
            h6{
                font-size: 15px;
            }
            p{
                font-size: 14px;
            }
        }
    }
}

// Profile Dropdown
.profile-toggle{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
}
.profileDropdownMenu{
    position: absolute;
    right: 0;
    left: auto!important;
    top: 50px!important;
    animation-name: dropdownAnimation;
    animation-duration: 0.25s;
    animation-fill-mode: both;
    .dropdown-item{
        margin-bottom: 0;
        padding: .65rem 1.5rem;
        cursor: pointer;
        display: flex;
    }
    .proDropdown-header {
        display: flex;
        padding: .65rem 1.5rem;
        border-bottom:1px solid rgba(120, 130, 140, 0.13);
        .image {
            width: 80px;
            height: 80px;
            border-radius: 5px;
            overflow: hidden;
            margin-right: 5px;
        }
        
        .proDropdown-content {
            padding-left: 10px;
            .title {
                font-size: 18px;
            }
            p {
                font-size: 13px;
            }
            .btn {
                border-radius: 50px;
                font-size: 12px;
                height: 30px;
                padding: 0;
                min-width: 90px;
                margin-top: 4px;
            }
        }
        
    }
}

.sub-menu{
    > .menu-item-has-children{
        >a{
            // position: relative;
            &:after{
                content: "\F140";
                display: inline-block;
                font-family: "Material Design Icons";
                transition: .4s;
            }
        }
        &.active{
            >a{
                &:after{
                    transform: rotate(-180deg);
                }
            }
        }
        &>.sub-menu + span.menu-expand{
            display: none;
        }
    }
}
.menu-item-has-children{
    .sub-menu + span.menu-expand{
        >i{
            transition: .4s;
        }
    }
    &.active{
        &>.sub-menu + span.menu-expand{
            &>i{
                transform: rotate(-180deg);
                
            }
        }
    }
}
