.btn{
    display: inline-flex;
    align-items: center;
    height: 40px;
    justify-content: center;
    font-weight: 400;
    padding-left: 22px;
    padding-right: 22px;
    min-width: 100px;
    font-size: 16px;
    background-color: $primary-color;
    border: 0;
    transition: .3s;
    &:hover{
        background-color: $primary-color;
        opacity: .7;
    }
